<template>
    <div class="page-content">
        <div class="content-nav">
            会话管理 &gt; 创建视频会话
        </div>
        <form @submit.prevent="createSession" @reset.prevent="resetSession">
            <fieldset>
                <legend>会话信息</legend>
                <table class="formtable">
                    <tbody>
                    <tr>
                        <td>类型:</td>
                        <td>
                            <select v-model="choosenSessionType">
                                <option v-for="type, index in meetingType" :value="index">{{type}}</option>
                            </select>
                        </td>
                    </tr>
                    <template v-if="choosenSessionType === 0">
                        <tr>
                            <td>视频流地址:</td>
                            <td>
                                <input type="text" v-model="streamUrl" required placeholder="rtsp://*/id=*">
                            </td>

                        </tr>
                        <tr>
                            <td>Video:</td>
                            <td>
                                <input type="checkbox" v-model="video"/>
                            </td>
                        </tr>
                        <tr>
                            <td>Audio:</td>
                            <td>
                                <input type="checkbox" v-model="audio"/>
                            </td>
                        </tr>
                    </template>

                    <template v-if="choosenSessionType === 1">
                        <tr>
                            <td>会话主题:</td>
                            <td>
                                <input id="capacity" type="text" v-model="meetingname" required placeholder="例如：流媒体" maxlength="20">
                            </td>
                        </tr>

                        <tr>
                            <td>备注：</td>
                            <td>
                                <textarea id="description" v-model="description"  maxlength="200" rows="5" cols="60" required placeholder="200字以内的文字描述"></textarea>
                            </td>
                        </tr>
                    </template>

                    <tr>
                        <td colspan="2" class="command">
                            <input type="submit" value="创建" class="clickbutton">
                            <input type="reset" value="重置" class="clickbutton">
                        </td>
                    </tr>
                    </tbody>
                </table>
            </fieldset>
        </form>
    </div>
</template>

<script>
    import HttpWsConnection from '@/js/httpws/httpWsConnection'
    import axios from 'axios'
    import trace from "../js/logging";
    export default  {
        name: 'create-meeting-content',
        data () {
            return {
                meetingname: '',
                description: '',
                groupId: 0,
                meetingType: ['单点视频会话', '多点视频会话', 'OneOnOne'],
                choosenSessionType: undefined,
                streamUrl: '',
                audio: false,
                video: true,
                wsConnection: null
            }
        },
        computed: {
            getSignalingServer () {
/*                trace('getSignalingServer:\r\n');
                trace(this.$store.getters.getSignalingServer);*/
                return this.$store.getters.getSignalingServer
            }
        },
        beforeDestroy () {
            trace('CreateMeetingContent beforeDestroy');
            if(this.wsConnection) {
                this.wsConnection.close();
                this.wsConnection = null;
            }
        },
        created () {
            trace('CreateMeetingContent created');
        },
        methods: {
            createSession() {
                if(this.choosenSessionType === undefined) {
                    alert('请选择要创建的视频会话类型');
                    return;
                }
                let meetingType = '';
                switch (this.choosenSessionType)  {
                    case 0:
                        meetingType = 'single';
                        this.singlePointHandler(meetingType);
                        break;
                    case 1:
                        meetingType = 'multi';
                        this.pttHandler(meetingType);
                        break;
                    case 2:
                        meetingType = 'peertopeer';
                        break;
                    default:
                        break;
                }
                console.log('create meeting: ' + this.streamUrl);
            },
            singlePointHandler(meetingType) {
                let self = this;
                axios.post('/webMedia/singlepoint/webrtc', {
                    rtsp: this.streamUrl,
                    video: this.video,
                    audio: this.audio
                })
                    .then( response => {
                        let data = response.data;
                        let signalingServer = `${self.getSignalingServer[0]}/endpoint${data.endpointId}`;
                        trace(signalingServer);
                        self.wsConnection = new HttpWsConnection(signalingServer);
                        self.wsConnection.onConnected = self.onWsConnected;
                    })
                    .catch( error => {
                        console.log(error);
                    });
            },
            pttHandler(meetingType) {

                let self = this;
/*                self.$router.push({
                    name: `${meetingType}`,
                    params: {groupId: 1}
                });
                return;*/
                axios.post('/webmedia/ptt/group', {
                    title: this.meetingname,
                    desc: this.description
                })
                    .then( response => {
                        let data = response.data;
                        trace(data);
                        self.$router.push({
                            name: `${meetingType}`,
                            params: {groupId: data.groupId}
                        });
                    })
                    .catch( error => {
                        console.log(error);
                    });
            },
            onWsConnected () {
                trace('onWsConnected');
                trace("meetingType:" + this.choosenSessionType);
                const options = {
                    method: 'post',
                    path: `/signalling-bridge/group/${++this.groupId}`
                };
                let self = this;
                if(this.wsConnection) {
                    let request_client = this.wsConnection.createClientRequest(options, res => {
                        if(res.ok) {
                            self.$router.push({
                                name: self.choosenSessionType === 0 ? 'single': 'multi',
                                params: {groupId: this.groupId}
                            });
                            return;
                            if(self.choosenSessionType > 0) {
                                self.createPipeline(meetingType);
                            } else {
                                self.$router.push({
                                    name: `${meetingType}`,
                                    params: {groupId: self.groupId}
                                });
                            }
                        } else {
                            alert(res.statusText);
                        }
                    });
                    request_client.end();
                }
            },
            resetSession() {
                this.choosenSessionType = undefined;
            },
            createPipeline(meetingType) {
                let self = this;
                axios.post(`/pipeline`, {
                    sourceUrl: self.streamUrl
                })
                    .then(function (response) {
                        self.$router.push({
                            name: `${meetingType}`,
                            params: {groupId: self.groupId}
                        });
                    })
                    .catch(function (error) {
                        console.log(error);
                    });
            }
        }
    }
</script>

<style scoped>
/*    .page-content{
        width:830px;
        float:right;
    }
    .page-content .content-nav{
        padding:0 0 10px 0;
    }*/
</style>



